a{
  text-decoration: none;
  color:#2b2b2b;
}

.NavbarWrap {
  width: 100%;
  height: 50px;
  border-bottom: 1px solid #e6e6e6;
}
.Navbar {
  width: 1280px;
  margin: 0 auto;
  display: flex;
  align-items: center;
  height: 50px;
  box-sizing: border-box;
}

.NavbarList {
  width:100%;
  list-style: none;
  display: flex;
  justify-content: center;
  gap:80px;
  align-items: center;
  box-sizing: border-box;
}
